import React, {useState} from 'react'
import styles from'./About.module.css';
import Topbar from '../common/Topbar'
import AOS from 'aos';
import 'aos/dist/aos.css';
import AnimatedCursor from "react-animated-cursor"
import { Col, Row} from 'antd';

export default function About() {
    AOS.init();

    const [isHover1, setisHoever1] = useState(false);
    const [isHover2, setisHoever2] = useState(false);

  return (
    <>
    <AnimatedCursor color='255, 255, 255'/>
    <Topbar/>

    <div className={styles.background}>
        <div data-aos="zoom-in" className={styles.top}>
            {/* <h2> */}
                "FrontEnd , ."
            {/* </h2> */}
        </div>

        <h4>
            About Me
        </h4>
        <div className={styles.middle}>
            <Row >
            <Col lg={8} md={24}>

                {
                    isHover1 ?
                    <img  src="code.gif" alt="" className={styles.imgsize}/>
                    :
                    isHover2 ?
                    <img  src="think.gif" alt="" className={styles.imgsize}/>
                    :
                    <div className={styles.imgdiv}>
                        <p className={styles.pImage}>    .</p>
                    </div>
                }
            </Col>
            <Col lg={8} md={24}>
                <p className={styles.p1}>
                     6      <underlinelight onMouseOver={()=>setisHoever1(true)} onMouseOut={() => setisHoever1(false)}></underlinelight>          . - Eagleson (Eagleson's Law)
                    <br/>
                    <br/>
                    <br/>
                </p>
                <p className={styles.p1}>
                       .      .        .             .
                </p>
            </Col>
            <Col lg={8} md={24}>
                <p className={styles.p1}>
                      , <underlinelight onMouseOver={()=>setisHoever2(true)} onMouseOut={() => setisHoever2(false)}>  </underlinelight>             .           .
                </p>
                <p className={styles.p1}>
                       , HTML, CSS, JavaScript        .       , DB ,        .
                </p>
            </Col>
            
            </Row>
        </div>

        <br/>
        <br/>

        <div>
        <h5>
               .
        </h5>
        <p className={styles.p1}>
            2017.02 ~ 2022.02 - /  
        <br/>
            2021.04 ~ 2021.10 - IITP/  - ARVR  
        </p>

        <br/>
        <br/>
        </div>

        <div>
        <h5>
               .
        </h5>
        <p className={styles.p1}>
            2021.10 ~ 2022.12 - ()  
        </p>

        <br/>
        <br/>
        </div>

        <div>
        <h5>
                .
        </h5>
        <p className={styles.p1}>
            HTML - CSS - JavaScript - TypeScript - React
        <br/>
            Swagger - Postman
        <br/>
            Git
        </p>
        </div>

        <br/>
        <br/>

    </div>
    </>
    
  )
}
